import React, { Component } from 'react';
import '../Section/loader'
import CircularSpinner from './circular.gif'

class ImageLoader extends Component {
  state = {
    src: `https://media1.giphy.com/media/3o7bu3XilJ5BOiSGic/giphy.gif?cid=ecf05e47icruwt1qtajdi4jre2wohir3rfmklf1ait72zkly&rid=giphy.gif&ct=g=${new Date().getMilliseconds()}`,
    loaded: false
  }

  onImageLoader = () => {
    this.setState({ loaded: true});
  }
  render(){
    const {src,loaded} = this.state;

    return (
      <div className="image-container">
        <img src={src} alt="img" onLoad={this.onImageLoader}/>
        { !loaded && <div className="image-container-overlay">
          <CircularSpinner />
          </div>}
      </div>
    );
  }
}

export default ImageLoader;